import React from 'react';
import Masonry, { ResponsiveMasonry } from 'react-responsive-masonry';
import '../styles/result.scss';
import heart from '../image/icon/icon-heart.png';

const Result = (props) => {
  const { loading,list,bookmark,onBookmarkChanged } = props;

  function handleBookmarkClick(e){
    onBookmarkChanged(e);
  }

  function renderMissing(){
    return (<div className="missing">未有符合要求資料</div>);
  }

  function renderLoading(){
    return (<div className="spinner"><span></span><span></span><span></span><span></span><span></span><span></span><span></span><span></span></div>);
  }

  function isBookmarked(id){
    if(!bookmark){
      return false;
    }
    const index = bookmark.indexOf(id);
    return index > -1;
  }

  function renderGrid(data){
    return (
      <div className="block" key={data[0][7]} >
        <div className="content" dangerouslySetInnerHTML={{__html: [data[0][10]]}} />
        <div className="bookmark">
          <div className={`icon ${isBookmarked(data[0][7]) ? "active" : ""}`} style={{ backgroundImage : 'url('+ heart +')' }} data-ig={data[0][7]} onClick={handleBookmarkClick}></div>
        </div>
      </div>
    )
  }

  return (
    <div id="result">
      <div className="container">
        {
          list.length > 0 &&
          <ResponsiveMasonry columnsCountBreakPoints={{ 375 : 1, 768: 2, 1080 :3 }}>
            <Masonry gutter={"20px"}>
              {
                list.map(a => renderGrid(a))
              }
            </Masonry>
          </ResponsiveMasonry>
        }
        {
          list.length === 0 && !loading && renderMissing()
        }
        {
           renderLoading()
        }
      </div>
    </div>
  )
}

export default Result;
