import React, { useState,useEffect } from 'react';
import Cookies from 'universal-cookie';
import Filter from './Filter.js';
import Result from './Result.js';
import '../styles/goods.scss';

const Goods = (props) => {
  const { showFilter,type,onFilterClose } = props;
  const [ members, setMembers ] = useState(["collar","candy","day","gao","ivy","marf","soching","sumling","winka"]);
  const [ types, setTypes ] = useState(["reserve","walkin","preview"]);
  const [ payments, setPayments ] = useState(["free","paid"]);
  const [ methods , setMethods ] = useState(["inPerson","sf","post","shop"]);
  const [ pageNum , setPageNum ] = useState(0);
  const [ dateRange , setDateRange ] = useState([
    {
      startDate: new Date(),
      endDate: null,
      key: 'selection'
    }
  ]);
  const [ loading , setLoading ] = useState(null);
  const [ hasMore , setHasMore ] = useState(true);
  const [ needMore , setNeedMore ] = useState(false);
  const [ filter , setFilter ] = useState(null);
  const [ bookmarks , setBookmarks ] = useState([]);
  const [ goods, setGoods ] = useState([]);
  const [ background, setBackground] = useState(null);
  const { innerWidth: width } = window;
  const cookies = new Cookies();

  function handleFilterClose(e){
    onFilterClose(e);
  }

  function addInfiniteScroll(){
    window.addEventListener('wheel',(e) => {
      var element = document.body;
      if(element.scrollHeight - window.innerHeight === window.scrollY)
      {
        setNeedMore(true);
      }
    }, { passive: true });
  }

  function clearGoods(){
    setGoods([]);
    setPageNum(0);
    setHasMore(true);
  }

  function handleDateChange(ranges){
    setDateRange([ranges.selection]);
    clearGoods();
  }

  function handleMembersChange(e){
    e.stopPropagation();
    var member = e.target.getAttribute("data-member");
    members.includes(member) ? setMembers(members.filter(m => m !== member)) : setMembers([...members,member]);
    clearGoods();
  }

  function handleTypeChange(e){
    e.stopPropagation();
    var type = e.target.getAttribute("data-type");
    types.includes(type) ? setTypes(types.filter(m => m !== type)) : setTypes([...types,type]);
    clearGoods();
  }

  function handlePaymentChange(e){
    e.stopPropagation();
    var payment = e.target.getAttribute("data-payment");
    payments.includes(payment) ? setPayments(payments.filter(m => m !== payment)) : setPayments([...payments,payment]);
    clearGoods();
  }

  function handleMethodChange(e){
    e.stopPropagation();
    var method = e.target.getAttribute("data-method");
    methods.includes(method) ? setMethods(methods.filter(m => m !== method)) : setMethods([...methods,method]);
    clearGoods();
  }

  function handleBookmarkChanged(e){
    var target = e.target.getAttribute("data-ig");
    var favList = bookmarks;
    if(!favList){
        favList = [];
    }
    const index = favList.indexOf(target);
    var newList = [];
    if (index > -1) {
      newList = favList.filter(b => b !== target);
    }else{
      newList = favList.concat([target]);
    }
    cookies.set("bookmarked",newList);
    setBookmarks(newList);
  }

  function buildFilter(){
    var filterArray = [];
    if(types.length > 0){
      filterArray.push("type="+types.join(','));
    }
    if(members.length > 0){
      filterArray.push("members="+members.join(','));
    }
    if(payments.length > 0){
      filterArray.push("payment="+payments.join(','));
    }
    if(methods.length > 0){
      filterArray.push("method="+methods.join(','));
    }
    if(pageNum > -1){
      filterArray.push("pageNum="+pageNum);
    }
    filterArray.push("startDate="+dateRange[0].startDate.toLocaleDateString("en-GB"));
    if(dateRange[0].endDate){
        filterArray.push("endDate="+dateRange[0].endDate.toLocaleDateString("en-GB"));
    }
    if(type === "bookmark"){
      var bookmarkArray = cookies.get("bookmarked");
      if(!bookmarkArray){
        bookmarkArray = [];
      }
      filterArray.push("id="+bookmarkArray.join(','));
    }
    var filterString = filterArray.join('&');
    setFilter(filterString);
  }

  function loadGoods(){
    setLoading(true);
    var url = 'https://script.google.com/macros/s/AKfycbw2o78iucpqXGSFR9CYIxECjrFnl_BTJUurAu3RNAinZjGp-MpCiH3ovgqphCAk1Baxtg/exec';
    if(!filter){
      setLoading(false);
      return;
    }
    url += "?"+filter;
    fetch(url)
    .then(function (response) {
      return response.text();
    }).then(function (html) {
      var newGoods = JSON.parse(html);
      if(newGoods.length === 0){
        setHasMore(false);
      }
      setNeedMore(false);
      setGoods(goods.concat(newGoods));
      setLoading(false);
    }).catch(function (err) {
      setNeedMore(false);
      setLoading(false);
      console.warn('Something went wrong.', err);
    });
  }

  useEffect(() => {
    var size = 720;
    if(width >= 1024){
      if(width > 2048){
        size = 3840;
      }else{
        size = 1440;
      }
    }
    var image = require('../image/background_'+size+'.png');
    setBackground(image);
    addInfiniteScroll();
    setBookmarks(cookies.get("bookmarked"));
    return () => {
      clearGoods();
    }
  }, []);

  useEffect(() => {
    clearGoods();
    buildFilter();
  }, [members,types,payments,methods]);

  useEffect(() => {
    if(needMore && !loading && hasMore){
      setPageNum(pageNum+1);
    }
  }, [needMore]);

  useEffect(() => {
    buildFilter();
  }, [pageNum]);

  useEffect(() => {
    loadGoods();
  }, [filter]);

  return (
    <section id="goods" style={{ backgroundImage : 'url('+ background +')' }}>
      {
        showFilter && <Filter
          members={members}
          payments={payments}
          types={types}
          methods={methods}
          date={dateRange}
          onClose={handleFilterClose}
          onMembersChange={handleMembersChange}
          onTypeChange={handleTypeChange}
          onPaymentChange={handlePaymentChange}
          onMethodChange={handleMethodChange}
          onDateChange={handleDateChange}
        />
      }
      <Result list={goods} loading={loading} bookmark={bookmarks} onBookmarkChanged={handleBookmarkChanged}/>
    </section>
  )
}

export default Goods;
